.container {
  --paddingContainer: 20px;
  max-width: 1200px;
  padding-left: var(--paddingContainer);
  padding-right: var(--paddingContainer);
  margin: 0 auto;

  @include onTablet {
    --paddingContainer: 41px;
  }

  @include onDesktopSmall {
    --paddingContainer: 55px;
  }
}
